.t{
    width:100%;
    display:flex;
  
}

.t .left{
    width:60%;
    padding-top: 50px;
    padding-right: 60px;
    margin-left:120px;
    margin-right:100px;
}
.t .left h1{
    font-size: 40px;
    font-weight: 700;
    text-align: left;
}

.t .left h4{
    padding:18px 0px;
    font-size: 15px;
    font-weight:lighter;
}

.t .left h3{
    font-size: 25px;
    padding-top: 20px;
    font-weight: 600;
}
.t .left p{
    font-size: 12px;
    padding-bottom: 20px;
}

hr{
    border-bottom: 1px solid #ececec;
}
.t .right {
    width:40%;
    padding-top: 30px;
    margin-left:10px;
    background-color: #F7F7F7;
    height: 100vh;
    
}

.t .right h4{
    font-size: 10px;
    color: black;
    margin-left: 30px;
    font-weight: 700;
}
.t .right ul{
    list-style: none;
    color: rgb(26, 26, 26);
    font-size: 12px;
    font-weight: 400;
    line-height: 18px; 
}

li.l {
    margin-left: -15px;
}
