.head{
    width:100%;
    display:flex;
    
    
}
.head .left{
    width:60%;
    padding-top: 50px;
    padding-right: 60px;
    margin-left:120px;
    margin-right:100px;
}
 .head .left h2{
     font-size: 40px;
     font-weight: 700;
     text-align: left;
 }
 .head .left h3{
     font-size: 20px;
     font-weight: 700;
     text-align: left;
     padding: 15px 0px;
 }

.head .left ul li{
    font-size: 10px;
}
 
.head .left h4{
    padding:18px 0px;
    font-size: 15px;
    font-weight:lighter;
}
.head .left p{
    font-size: 12px;
    padding-bottom: 18px;
}
.head .left p i{
    margin-left: 5px;
}

.head .right {
    width:40%;
    margin-top:0px;
    padding-top: 30px;
    margin-left:10px;
    background-color: #F7F7F7;
    height: 100vh;

}
.head .right ul li {
    list-style: none;
    color: #6D6D6D;
    font-size: 10px;
    font-weight: 600;
    line-height: 40px;
}
