.b{
    width:100%;
    display:flex;
    
}

.b .left{
    width:60%;
    padding-top: 50px;
    padding-right: 60px;
    margin-left:120px;
    margin-right:100px;
}

.b .left h1{
    font-size: 40px;
     font-weight: 700;
     text-align: left;
}

.b .left h4{
    padding: 10px 0px;
    font-size: 15px;
    font-weight: lighter;  
}

.b .left p{
    font-size: 12px;
    padding-bottom: 15px;
}

.b .left ul{
    font-size: 12px;
  
}

.b .right{
    width:40%;
    margin-top:0px;
    padding-top: 30px;
    margin-left: 5px;
    background-color: #F7F7F7;
    height:100vh;
    
}
.b .right h4{
    font-size: 12px;
    font-weight: 700;
    margin-left: 40px;

}

.b .right ul li{
    list-style: none;
    color: black;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
}