.h{
    width:100%;
}
.h .k{
    width:100%;
    
    
  
}
.h .lower .middle a{
    text-decoration: none;
    color: white;
}

.h .upper{
    width:100%;
    color: white;
    height: 50px;
    padding:10px 0px;
    text-align:center;
    font-size: 16px;
    background-color: hsl(222, 14%, 10%);
   
}
.h .upper .first a{
    text-decoration: none;
    color:#61DAFB ;

    
}
.h .lower{
    width:100%;
    display:flex;
    color: white;
    height: 50px;
    padding:10px 50px;
    vertical-align: center;
    background-color: #20232A;
    
}
.h .lower .left{
    color: #61DAFB;
}
.h .lower .left a{
    display:flex;
    font-size: 16px;
    font-weight: 200;
    text-decoration: none;
}
.h .lower .left h4{
    padding-left: 4px;
    font-size: 23px;
    color:#61DAFB;
    font-weight: 600;
  
    
}
.h .lower .left  img{
    width:25px;
    height:25px;
}

.h .middle ul{
    display:flex;
    list-style: none;
}

.h .middle ul li{
    font-size: 14px;
    font-weight: lighter;
    margin-right:20px;
}

.h .last ul{
    display: flex;
    list-style: none;
}
.h .last ul li{
    font-size: 14px;
    font-weight: 100px;
    margin-right:15px;
}
.h .last ul li i{
    margin-right:4px;
}
.h .last ul li input{
    background-color: #20232A;
    border: none;
}